import { Entity, Required, Type } from '@juneil/entityts';

export class AddressEntity extends Entity {
    @Required(Entity.Mode.READ, Entity.Mode.UPDATE, Entity.Mode.CREATE)
    @Type(String)
    street: string;

    @Required(Entity.Mode.READ, Entity.Mode.UPDATE, Entity.Mode.CREATE)
    @Type(String)
    postalCode: string;

    @Required(Entity.Mode.READ, Entity.Mode.UPDATE, Entity.Mode.CREATE)
    @Type(String)
    city: string;
}
